.scene.surveillance {
  background-color: #17684D;
}

.videogrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 16px;
  grid-row-gap: 16px;
  width: 100%;
}

.videoframe {
  background-color: #10493A;
  border-radius: 8px;
  width: auto;
  height: 0;
  padding-bottom: 56.25%;
  position: relative;
  transform: scale(0);
  animation: 0.4s bounceIn forwards;
  overflow: hidden;
}

.videoframeContainer {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  justify-content: center;
  align-items: center;
}

@keyframes bounceIn {
   0% {
     opacity: 0;
     transform: scale(.3);
   }
   50% {
     transform: scale(1.05);
   }
   70% {
     opacity: 1;
     transform: scale(.9);
   }
   100% {
     transform: scale(1);
   }
}
