body {
  line-height: 1.6;
}

p:not(:last-of-type) {
  margin-bottom: 1em;
}

.textSmall {
  font-size: 0.9em;
}

.textMedium {
  font-weight: 500;
}

.textSemibold {
  font-weight: 600;
}

.textScrambled {
  font-family: 'Marlett';
  text-transform: lowercase;
}

.textHeading {
  font-size: 2em;
  margin-bottom: 0.9em;
  color: #354050;
}

@font-face {
  font-family: 'Marlett';
  src: url('./fonts/marlett.eot');
  src: url('./fonts/marlett.eot?#iefix') format('embedded-opentype'),
       url('./fonts/marlett.woff2') format('woff2'),
       url('./fonts/marlett.woff') format('woff'),
       url('./fonts/marlett.ttf') format('truetype'),
       url('./fonts/marlett.svg#marlettregular') format('svg');
  font-weight: normal;
  font-style: normal;
}
