.overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  transition: opacity 0.64s cubic-bezier(0.65,0.05,0.36,1);
  pointer-events: none;
}

.overlay.visible {
  opacity: 1;
}

.vignette {
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, transparent 50%, rgba(0, 0, 0, 0.16) 150%);
  pointer-events: none;
}

.vignetteFilter {
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(0, 0, 0, 0.15) 50%, rgba(0, 0, 0, 0.30) 150%);
  transition: opacity 0.64s cubic-bezier(0.79,0.14,0.15,0.86);
  opacity: 0;
}

.vignetteFilter.enabled {
  backdrop-filter: blur(0.5em) sepia(0.6);
}

.vignette.dim {
  pointer-events: all;
}

.vignette.dim .vignetteFilter {
  opacity: 1;
}

.bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  min-height: 80px;
  background-color: rgba(16, 19, 23, 0.7);
  box-shadow: 0 -16px 32px -24px rgba(0, 0, 0, 0.2);
  color: #fff;
  pointer-events: all;
}

.visible .bar {
  animation: slide-in-bottom 1s cubic-bezier(0.22,0.61,0.36,1) both;
  animation-delay: 2s;
}

.bar .buttonGroup {
  display: flex;
}

.barButton {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  min-height: 48px;
  background: transparent;
  color: rgba(255, 255, 255, 0.8);
  border-radius: 8px;
  position: relative;
  padding: 8px 24px;
  margin: 0 16px;
  font-size: 0.7em;
  font-family: inherit;
  cursor: pointer;
  transition: transform 0.1s cubic-bezier(0.65,0.05,0.36,1);
}

.barButton:enabled:hover {
  color: #fff;
  background-color: rgba(0, 0, 0, 0.5);
}

.barButton:enabled:active {
  transform: translateY(2px);
  background-color: rgba(0, 0, 0, 0.9);
}
.barButton svg {
  opacity: 0.8;
  margin-bottom: 8px;
  transition: transform 0.64s cubic-bezier(0.77,0,0.18,1);
}

.barButton[name='info'] svg {
  opacity: 0.9;
}

.barButton:enabled:hover svg {
  opacity: 1;
}

.leaveButton {
  background-color: #9E2C2C;
  margin: 0 16px;
  font-size: 0.9em;
}

.leaveButton:hover:not(:active) {
  background-color: #bf2e2e;
}

.inaccessible .barButton {
  color: rgba(0, 0, 0, 0);
}

.inaccessible .barButton[name='previous'] svg,
.inaccessible .barButton[name='next'] svg {
  transform: rotate(180deg);
}

.inaccessible .leaveButton,
.inaccessible .leaveButton:hover {
  background: transparent;
  transform: none;
  box-shadow: none;
  color: #bf2e2e;
}

@keyframes slide-in-bottom {
  0% {
    -webkit-transform: translateY(1000px);
            transform: translateY(1000px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
