.button {
  min-height: 48px;
  color: #fff;
  background-color: #3582E8;
  border-radius: 8px;
  position: relative;
  padding: 12px 40px;
  font-size: 1em;
  font-family: inherit;
  cursor: pointer;
  box-shadow: 0 12px 16px -8px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.2s cubic-bezier(0.65,0.05,0.36,1), transform 0.1s cubic-bezier(0.65,0.05,0.36,1);
}

.button::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: opacity 0.1s cubic-bezier(0.65,0.05,0.36,1);
  opacity: 0;
  border-radius: 8px;
  background-color: rgba(0, 0, 0, 0.1);
}

.button:hover {
  transform: translateY(-1px);
  box-shadow: 0 10px 32px -8px rgba(0, 0, 0, 0.44);
}

.button:hover:not(:active) {
  background-color: #3595e8;
}

.button:active {
  transform: translateY(3px);
  box-shadow: 0 8px 16px -8px rgba(0, 0, 0, 0.16);
  transition-duration: 0.1s;
}

.button:active::after {
  opacity: 1;
}
